import { useCallback, useEffect, useMemo, useState } from 'react';

import * as anchor from '@project-serum/anchor';
import { addNFTindex, getNFTImage, getNFTRarity, getNFTRScore } from '../utils/NFTmetadata';
import { trait } from '../constants/interfaces';

export interface TraitProps {
    candyMachineId?: anchor.web3.PublicKey;
    connection: anchor.web3.Connection;
}



const Trait = (props: TraitProps) => {
    const [name, setName] = useState<string>("");
    const [image, setImage] = useState<string>("");
    const [attributes, setAttributes] = useState<any>();
    const [prevIndex, setPrevIndex] = useState<number>(90);

    const [backgroundRarity,setBackgroundRarity] = useState<number>(0)
    const [skinRarity,setSkinRarity] = useState<number>(0)
    const [eyesRarity,setEyesRarity] = useState<number>(0)
    const [mouthRarity,setMouthRarity] = useState<number>(0)
    const [specialRarity,setSpecialRarity] = useState<number>(0)
    const [headRarity,setHeadRarity] = useState<number>(0)
    const [clothingRarity,setClothingRarity] = useState<number>(0)
    const [accesoryRarity,setAccesoryRarity] = useState<number>(0)
    const [wildFriendRarity,setWildFriendRarity] = useState<number>(0)

    const [backgroundScore,setBackgroundScore] = useState<number>(0)
    const [skinScore,setSkinScore] = useState<number>(0)
    const [eyesScore,setEyesScore] = useState<number>(0)
    const [mouthScore,setMouthScore] = useState<number>(0)
    const [specialScore,setSpecialScore] = useState<number>(0)
    const [headScore,setHeadScore] = useState<number>(0)
    const [clothingScore,setClothingScore] = useState<number>(0)
    const [accesoryScore,setAccesoryScore] = useState<number>(0)
    const [wildFriendScore,setWildFriendScore] = useState<number>(0)

    const [loadedData, setLoadedData] = useState<boolean>(false);
    const [loadedRarity, setLoadedRarity] = useState<boolean>(false);
    const [loadedScore, setLoadedScore] = useState<boolean>(false);

    const [inputValue, setInputValue] = useState("");

    const onChangeHandler = (event: any) => {
        setInputValue(event.target.value);
    };

    async function poblateData(index: number = 90) {
        let response = await getNFTImage(index)
        console.log(response)
        //console.log(response?.attributes[trait.Background]['value'])
        if (response) {
            setName(response.name)
            setImage(response.image)
            setAttributes(response.attributes)
            setPrevIndex(response.prevIndex)
            setLoadedData(true)
        }
        else {
            alert("Error")
        }
    }
    async function poblateRarity(index: number = 90) {
        let response = await getNFTRarity(index);
        if (response) {
            setBackgroundRarity(response.BackgroundRarity)
            setHeadRarity(response.HeadRarity)
            setSkinRarity(response.SkinRarity)
            setEyesRarity(response.EyesRarity)
            setMouthRarity(response.MouthRarity)
            setSpecialRarity(response.SpecialRarity)
            setClothingRarity(response.ClothingRarity)
            setAccesoryRarity(response.AccesoryRarity)
            setWildFriendRarity(response.WildFriendRarity)

            setLoadedRarity(true)
        }
        else {
            alert("Error")
        }
    }

    async function poblateScore(index: number = 90) {
        let response = await getNFTRScore(index);
        if (response) {
            setBackgroundScore(response.BackgroundScore)
            setHeadScore(response.HeadScore)
            setSkinScore(response.SkinScore)
            setEyesScore(response.EyesScore)
            setMouthScore(response.MouthScore)
            setSpecialScore(response.SpecialScore)
            setClothingScore(response.ClothingScore)
            setAccesoryScore(response.AccesoryScore)
            setWildFriendScore(response.WildFriendScore)

            setLoadedScore(true)
        }
        else {
            alert("Error")
        }
    }

    async function poblateGeneral(index: number = 90){
        await poblateData(index)
        await poblateRarity(index)
        await poblateScore(index)
    }

    useEffect(() => {
        const fetchData = async () => {
            await poblateGeneral()
        }

        fetchData()
    }, [])



    async function searchNFT() {

    }


    return (
        <div className="traits-container">
            {(loadedData && loadedRarity && loadedScore) && (
                <div className="row-traits">
                    <div className="left-traits">
                        <div className="display-image">
                            <img src={image} alt="nft image" />
                            <i onClick={() => {poblateGeneral(prevIndex - 1)}} className="uil uil-angle-left"></i>
                            <i onClick={() => {poblateGeneral(prevIndex + 1)}} className="uil uil-angle-right"></i>
                        </div>
                        <div className="function-traits">
                            <div className="name-trait">
                                <h2>{name}</h2>
                            </div>
                            <div className="global-rarity">
                                <div className="rarity-rank">
                                    <p>Rarity Rank</p>
                                    <h3>#0000</h3>
                                </div>
                                <div className="rarity-score">
                                    <p>Rarity Score</p>
                                    <h3>0000</h3>
                                </div>
                            </div>
                            <div className="query-traits">
                                <div className="query-up">
                                    <span onClick={()=> window.open(image, "_blank")}>IMAGE</span>
                                    <span className='trait-magiceden'>VIEW ON MAGICEDEN</span>
                                </div>
                                <div className="query-down">
                                    <input onChange={onChangeHandler} type="text" placeholder='Enter #' />
                                    <span onClick={() => { poblateGeneral(parseInt(inputValue)) }}>GO</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="right-traits">
                        <div className="detail-traits">
                            <h1>RARITY STATS</h1>
                            <div className="description-box">
                                <div className="description-left">
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Background</h4>
                                            <p>{backgroundScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Background]['value']}</p>
                                            <p>{backgroundRarity}%</p>
                                        </div>
                                    </div>
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Skin</h4>
                                            <p>{skinScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Skin]['value']}</p>
                                            <p>{skinRarity}%</p>
                                        </div>
                                    </div>
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Eyes</h4>
                                            <p>{eyesScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Eyes]['value']}</p>
                                            <p>{eyesRarity}%</p>
                                        </div>
                                    </div>
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Mouth</h4>
                                            <p>{mouthScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Mouth]['value']}</p>
                                            <p>{mouthRarity}%</p>
                                        </div>
                                    </div>
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Special</h4>
                                            <p>{specialScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Special]['value']}</p>
                                            <p>{specialRarity}%</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="description-right">
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Head</h4>
                                            <p>{headScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Head]['value']}</p>
                                            <p>{headRarity}%</p>
                                        </div>
                                    </div>
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Clothing</h4>
                                            <p>{clothingScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Clothing]['value']}</p>
                                            <p>{clothingRarity}%</p>
                                        </div>
                                    </div>
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Accesory</h4>
                                            <p>{accesoryScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.Accesory]['value']}</p>
                                            <p>{accesoryRarity}%</p>
                                        </div>
                                    </div>
                                    <div className="trait-rarity">
                                        <div className="tr-up">
                                            <h4>Wild Friend</h4>
                                            <p>{wildFriendScore}</p>
                                        </div>
                                        <div className="tr-down">
                                            <p>{attributes[trait.WildFriend]['value']}</p>
                                            <p>{wildFriendRarity}%</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    )
}

export default Trait;