import { Connection, PublicKey } from '@solana/web3.js';
import { TOKEN_PROGRAM_ID } from '@solana/spl-token';
import axios from 'axios';
import * as Metadata from "@metaplex-foundation/mpl-token-metadata";
import { getCandyMachineCreator, getMintAddresses } from './fetchholders';
import { getNFTMetadata } from './NFTget';
import DATA from '../constants/data';
import * as fs from 'fs';
import MINTS from '../constants/mints';
import NFTINDEX from '../constants/nftindex';
import DATATRAIT from '../constants/dataafter';
import { NFTdata, NFTRarity, NFTRarityScore, NFTRarityValue, NFTRarityValueObj, trait, traitCuantity } from '../constants/interfaces';

async function getAllMints(
    conn: Connection,
) {
    const candyMachineCreator = await getCandyMachineCreator(new PublicKey("GzwVNoqEUTUUkcXCxbLNDyGDv8Ns1cExa61h4nNDcVcz"));
    let mints = await getMintAddresses(candyMachineCreator[0], conn);
    return mints;
}

export async function addNFTValue(
    conn: Connection,
) {
    let arrMints = await getAllMints(conn)
    let dataTraits = DATA;

    console.log(arrMints)
    //let data = await getNFTMetadata(arrMints[0], conn)
    arrMints.forEach(async (mint) => {
        let data = await getNFTMetadata(mint, conn)
        for (let x = 0; x < data?.externalMetadata.attributes.length; x++) {
            let trait_type = await data?.externalMetadata.attributes[x].trait_type
            let value = await data?.externalMetadata.attributes[x].value
            if (trait_type == 'Wild Friend') {
                trait_type = 'WildFriend'
            }
            dataTraits[trait_type][value] += 1
        }
    });
}

export async function addNFTindex(
    conn: Connection,
) {
    let arrMints = MINTS;
    let objects: any = []
    let count = 0

    await new Promise((resolve, reject) => {
        arrMints.forEach(async (mint) => {
            try {
                let data = await getNFTMetadata(mint, conn)
                let externalData = await data?.externalMetadata
                objects.push(externalData)
            } catch (error) {
                console.log(error)
            } finally {
                count += 1
                if (count == arrMints.length) {
                    resolve(objects)
                }
            }
        })
    })

    let dic: any = {}

    for (let data of objects) {
        let number = data['name'].split('#')[1]
        dic[number] = data
    }

    console.log(dic)
}

export async function getNFTRarity(
    index: number = 90,
): Promise<NFTRarityValue | undefined> {
    let mintData = NFTINDEX;
    let dataTrait = DATATRAIT

    let attributes = mintData[index.toString()]['attributes']

    let obj = NFTRarityValueObj

    for (let x = 0; x < attributes.length; x++) {
        let trait_value = attributes[x]['trait_type']
        let value = attributes[x]['value']
        let rarity = "0"
        if (trait_value != "Wild Friend") {
            rarity = (dataTrait[trait_value][value] * 100 / 30).toFixed(2);
            obj[trait_value] = rarity;
        }
        else {
            console.log(dataTrait["WildFriend"][value])
            rarity = (dataTrait["WildFriend"][value] * 100 / 30).toFixed(2);
            obj["WildFriend"] = rarity;
        }
    }

    return {
        BackgroundRarity: obj["Background"],
        SkinRarity: obj["Skin"],
        EyesRarity: obj["Eyes"],
        MouthRarity: obj["Mouth"],
        SpecialRarity: obj["Special"],
        HeadRarity: obj["Head"],
        ClothingRarity: obj["Clothing"],
        AccesoryRarity: obj["Accesory"],
        WildFriendRarity: obj["WildFriend"]
    }
}

export async function getNFTRScore(
    index: number = 90,
): Promise<NFTRarityScore | undefined> {
    let mintData = NFTINDEX;
    let dataTrait = DATATRAIT

    let attributes = mintData[index.toString()]['attributes']

    let obj = NFTRarityValueObj

    for (let x = 0; x < attributes.length; x++) {
        let trait_value = attributes[x]['trait_type']
        let value = attributes[x]['value']
        let score = "0"
        if (trait_value != "Wild Friend") {
            score = (dataTrait[trait_value][value] * 100 * traitCuantity[trait_value] / 30).toFixed(2);
            obj[trait_value] = score;
        }
        else {
            score = (dataTrait["WildFriend"][value] * 100 * traitCuantity['WildFriend'] / 30).toFixed(2);
            obj["WildFriend"] = score;
        }
    }

    return {
        BackgroundScore: obj["Background"],
        SkinScore: obj["Skin"],
        EyesScore: obj["Eyes"],
        MouthScore: obj["Mouth"],
        SpecialScore: obj["Special"],
        HeadScore: obj["Head"],
        ClothingScore: obj["Clothing"],
        AccesoryScore: obj["Accesory"],
        WildFriendScore: obj["WildFriend"]
    }
}


export async function getNFTImage(
    index: number = 90
): Promise<NFTdata | undefined> {
    let mintData = NFTINDEX;

    return {
        name: mintData[index.toString()]['name'],
        image: mintData[index.toString()]['image'],
        attributes: mintData[index.toString()]['attributes'],
        prevIndex: index
    }
}